@media only screen and (max-width: 500px){

.list{
    flex-direction: column;
    height: auto;
    /* display: none; */
}
.list li :nth-child(1){
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    margin-top: 50px;
}
.list li {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    text-align: center;
    margin-right: 0px;
    padding: 20px 0;
}

.hamburger-menu{
   
  display: block;
  
}




/* ------------------------------------------- */

  .home{
    height: 92vh;
    width: 100%;
    display: flex;
    align-items:center;
    justify-content: center;
    flex-direction: column;
  
  }
  
  .name{
    line-height: 1.14286;
    letter-spacing: .01em;
  
    font-weight: 400;
  
    font-size: 12px;
  
    margin-top: 0;
    margin-bottom: 10px;
  }
  
  
  .title{
    font-size: 24px;
    line-height: 1.0625;
    font-weight: 600;
    letter-spacing: -.01em;
    text-transform: none;
    margin-bottom: 10px;
  }
  .boi{
    line-height: 1.14286;
    letter-spacing: .01em;
    font-weight: 400;
    text-align: center;
  }
  /* -----------ABOUTUS------------------------- */
  
  .details ,.projects{
    font-size: 12px;
  }
  .school, .project, .exp{
    font-size: 8px;
  }
  .gradution{

    font-size: 8px;
  }
  /* ------------------------------ */
  .column{
    width: 100%;
  }
}