/* Homepage Component CSS */

.main {
    padding: 2rem;
  }
  
  .home {
    height: 92vh;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    /* flex-direction: row; */
  }
  
  .home-content {
    text-align: center;
    /* color: white; */
  }
  
  .name {
    font-size: 3rem;
    line-height: 1.2;
    margin-bottom: 0.5rem;
  }
  
  .title {
    font-size: 5rem;
    font-weight: bold;
    line-height: 1.2;
    margin-bottom: 1rem;
  }
  
  .boi {
    font-size: 1.5rem;
    line-height: 1.5;
    margin-bottom: 2rem;
  }
  
  .btn {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }
  
  .resume,
  .hireme {
    background-color: black;
    border: none;
    padding: 1rem 2rem;
    margin-right: 1rem;
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    border-radius: 7px;
  }
  
  .resume:hover,
  .hireme:hover {
    background-color: rgb(87, 17, 17);
    color: white;
  }
  
  .home-img {
    margin-top: 2rem;
  }
  
  .home-img img {
    width: 200px;
    height: auto;
    border-radius: 50%;
  }
  